import {Directive, ElementRef, Input, Output, OnChanges, EventEmitter} from "@angular/core";

@Directive({
	selector: '[contenteditableModel]',
	host: {
		'(blur)': 'onBlur()'
	}
})
export class ContenteditableModel implements OnChanges {
	@Input('contenteditableModel') model: any;
	@Output('contenteditableModelChange') update = new EventEmitter();
 
	private lastViewModel: any;
 
 
	constructor(private elRef: ElementRef) {
	}
 
	ngOnChanges(changes) {
		//console.log(changes)
		//console.log(this.lastViewModel)
		//if (isPropertyUpdated(changes, this.lastViewModel)) {
			if(changes.model.firstChange || changes.model.currentValue !== this.lastViewModel.currentValue){
			this.lastViewModel = this.model
			this.refreshView()
			}
		//}
	}
 
	onBlur() {
		var value = this.elRef.nativeElement.innerText
		this.lastViewModel = value
		this.update.emit(value)
	}
 
	private refreshView() {
		this.elRef.nativeElement.innerText = this.model
	}
}


